.cont(@width_content, @width_margin) {
    max-width: @width_content;
    width: 100%;
    padding-right: @width_margin;
    padding-left: @width_margin;
    margin-right: auto;
    margin-left: auto;
}

.container {
    max-width: @width_layout;
    width: 100%;
    margin-right: auto;
    margin-left: auto;
}
  
.content {
    .cont(@width_content, @width_margin)
}

.flex {
    display:flex;
    flex-wrap:wrap;
}

.flex-between {
    .flex;
    justify-content: space-between; 
}

.flex-around {
    .flex;
    justify-content: space-around;
}

.flex-center {
    .flex;
    justify-content: center;
}

.flex-end {
    .flex;
    justify-content: flex-end;
}

.flex-center {
    .flex;
    justify-content: center;
}

.cell-1 {
    width: 100%
}

.cell-2 {
    width: 100% / 2;
}

.cell-3 {
