}

.cell-12 {
    width: 100% / 12;
}

.grid-sa(@cell, @margin-lr, @margin-tb: 0) {
    .cell {
        margin-left: @margin-lr / 2;
        margin-right: @margin-lr / 2;
        margin-top: @margin-tb / 2;
        margin-bottom: @margin-tb / 2;
        width: 100 / @cell - @margin-lr ;
    }
}

.grid-sb(@cell, @margin-lr, @margin-tb: 0) {
    padding-left: @margin-lr / 2;
    padding-right: @margin-lr / 2;
    .flex {
        margin-left: -@margin-lr / 2;
        margin-right: -@margin-lr / 2;
        .grid-sa(@cell, @margin-lr, @margin-tb);
    }
}

.md(@width, @style) {
    @media screen and (max-width: @width) {
        @style();
    }
}