/* Стили сайта */

/* Временные стили */

.header,
.material,
.work-info,
.details {
  padding-bottom: 180px;
  text-align: center;
}

.header {
  background-image: url(../img/header_fon.jpg);
  padding-bottom: 666px;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 0;
}

.details {
  background-color: #dfdfdf;
}

/* Временные стили */

.section_title {
  font-size: 40px;
  font-weight: 700;
  text-align: center;
}

.slick-prev:before,
.slick-next:before {
  color: red;
  font-size: 30px;
  opacity: 1;
  content: none;
}

.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  color: white;
  background-color: red;
}

.slick-prev,
.slick-next {
  font-size: 30px;
  background-color: red;
  color: white;
  border-radius: 25px;
  width: 30px;
  height: 30px;
}

.slick-slide {
  margin: 0 15px;
}

.slick-list {
  margin: 0 -15px;
}

/* .header */

.header_top {
  padding: 40px 0;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  color: white;
  text-shadow: 1px 1px black;
  max-width: 1200px;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.header_description {
  width: 50%;
}

.header_logo,
.header_kontakty {
  width: 25%;
}

/* .example-work */

.example-work {
  padding-top: 50px;
  text-align: center;
}

.example-work__content_gallery .cell {
  margin-left: 0.5%;
  margin-right: 0.5%;
  margin-top: 5px;
  margin-bottom: 5px;
  width: 32.33333333%;
}

.example-work__content_gallery_elem {
  border: 1px solid #d0c7c7;
  border-radius: 5px;
  background-color: #efefef;
  padding: 3px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  flex-direction: column;
}

.example-work__content_gallery_elem_desc {
  font-size: 18px;
}

.example-work__content_gallery_elem_img {
  position: relative;
  cursor: pointer;
}

.example-work__content_gallery_elem_img img {
  display: block;
  border-radius: 5px;
  border: 1px solid #ada9a9;
}

.example-work__content_gallery_elem_img:hover .example-work__content_gallery_elem_img_overlay {
  display: block;
}

.example-work__content_gallery_elem_img:hover .example-work__content_gallery_elem_img_text {
  display: block;
}

.example-work__content_gallery_elem_img_overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: black;
  opacity: 0.5;
  display: none;
  border-radius: 5px;
}

.example-work__content_gallery_elem_img_text {
  position: absolute;
  width: 100%;
  display: none;
  color: white;
  top: 30%;
  font-size: 30px;
  user-select: none;
}

.example-work__content_gallery_elem_desc {
  font-size: 16px;
  font-weight: 400;
}

.example-work__content_gallery_elem_desc ul {
  text-align: left;
  margin-left: 40px;
  margin-top: 5px;
  margin-bottom: 10px;
  list-style: none;
}

.example-work__content_gallery_elem_desc ul li {
  display: flex;
  align-items: center;
}

.example-work__content_gallery_elem_desc ul li:before {
  content: '';
  padding-left: 3px;
  height: 5px;
  width: 5px;
  background-color: black;
  border-radius: 5px;
  display: block;
  margin-right: 5px;
}

.example-work__content_title {
  font-size: 25px;
  font-weight: bold;
  margin: 10px 0;
}

.example-work__content_gallery_elem_wrap_btn {
  margin: 5px 0;
}

.example-work__content_gallery_elem_name {
  font-size: 25px;
  font-weight: bold;
}

.example-work__content_gallery_elem_btn {
  margin: 5px 0;
  font-size: 22px;
  font-weight: bold;
  border-radius: 5px;
  color: white;
  background-color: red;
  box-shadow: 1px 5px #790808;
  outline: none;
  border: none;
  transition: all 0.4s;
}

.example-work__content_gallery_elem_btn:active {
  margin-top: 10px;
  box-shadow: 0 0 #02024a;
  margin-bottom: 0;
}

.example-work__content_more {
  margin: 10px 0;
}

/* designer */

.show_constuctor .designer__opacity-layout {
  display: none;
}

.show_constuctor .designer__constructor-layout {
  display: block;
}

.designer {
  text-align: center;
  background-color: #dfdfdf;
}

.designer__options {
  text-align: left;
  padding: 45px;
  width: 30%;
}

.designer__title {
  font-size: 40px;
}

.designer__opacity-layout {
  min-height: 286px;
}

.designer__constructor-layout {
  display: none;
}

.designer__constructor-layout-description {
  max-width: 350px;
  width: 100%;
  margin: 0 auto;
}

.designer__constructor {
  width: 70%;
}

.designer__constructor-elem {
  transform: rotateY(90deg);
  transition: all 0.3s;
  display: none;
  height: 100%;
  width: 100%;
}

.designer__constructor-elem-pipe {
  transform: rotateY(90deg);
  transition: all 0.3s;
  max-width: 66px;
  display: none;
}

.designer__constructor-layout-top {
  min-height: 100px;
}

.designer__constructor-layout-top img {
  margin: 0 auto;
}

.designer__constructor-layout-bottom {
  width: fit-content;
  margin: 0 auto;
}

/* material */

.material__slider {
  max-width: 960px;
  width: 100%;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
}

.material__slider_img {
  border-radius: 5px;
  border: 1px solid #ada9a9;
}

.material__slider-title {
  font-size: 25px;
}

/* .footer */

.footer {
  background-color: black;
  color: white;
}

/* .fancybox */

.fancybox-navigation .fancybox-button--arrow_right {
  margin-right: 80px;
}

.fancybox-navigation .fancybox-button--arrow_left {
  margin-left: 80px;
}

@media screen and (max-width: 1280px) {
  .fancybox-navigation .fancybox-button--arrow_right {
    margin-right: 0;
  }

  .fancybox-navigation .fancybox-button--arrow_left {
    margin-left: 0;
  }
}