@import url(libs/my_libs.less);
@import url(fonts/fonts.less); // Шрифты 

@width_layout: 1366px;
@width_content: 1000px;
@width_margin: 15px;

body {
    font-size: 16px;
    color: black;
    font-family: "Roboto";
}


/* Стили сайта */

/* Временные стили */

.header, .material, .work-info, .details{
    padding-bottom: 180px;
    text-align: center;
}

.header {
    background-image: url(../img/header_fon.jpg);
    padding-bottom: 666px;
    background-repeat: no-repeat;
    background-size: cover;
    padding-top: 0;
}

.material {
    //background-color: #505024;
}

.work-info {
    //background-color: #dfdfdf;
} 

.details {
    background-color: #dfdfdf;
}


/* Временные стили */

.section_title {
	font-size: 40px;
    font-weight: 700;
  	text-align: center;
}

.slick-prev:before, .slick-next:before {
	color: red;
	font-size: 30px;
	opacity: 1;
	content: none;
}

.slick-prev:hover, .slick-prev:focus, .slick-next:hover, .slick-next:focus {
	color: white;
	background-color:red;
}

.slick-prev, .slick-next {
	font-size: 30px;
	background-color: red;
	color: white;
	border-radius: 25px;
	width: 30px;
	height: 30px;
}

.slick-slide {
	margin: 0 15px;
}

.slick-list {
	margin: 0 -15px;
}

/* .header */
.header {
    &_top {
        padding: 40px 0;
        font-size: 16px;
        font-weight: bold;
        text-align: center;
        color: white;
        text-shadow: 1px 1px black;
        .cont(1200px, @width_margin);
    } 

    &_description, {
         width: 50%;
    }

    &_logo, &_kontakty {
        width: 25%;
    }
}

/* .example-work */
.example-work {
    padding-top: 50px;
    text-align: center;
    &__content_gallery {
        .grid-sa(3, 1%, 10px);
        &_elem {
            border: 1px solid #d0c7c7;
            border-radius: 5px;
            background-color: #efefef;
            padding: 3px;
            .flex-between;
            flex-direction: column;
            &_desc {
                font-size: 18px;
            }
        }
    }

    &__content_gallery_elem_img {
        position: relative;
        cursor: pointer;
        img {
            display: block;
            border-radius: 5px;
            border: 1px solid #ada9a9;
        }
        &:hover .example-work__content_gallery_elem_img_overlay {
            display: block;
        }
        &:hover .example-work__content_gallery_elem_img_text {
            display: block;
        }
        &_overlay {
            position:absolute;
            width:100%;
            height:100%;
            background-color:black;
            opacity:0.5;
            display:none;
            border-radius:5px;
        }
        &_text {
            position:absolute;
            width:100%;
            display:none;
            color:white;
            top:30%;
            font-size:30px;
            user-select: none;
        }
    }

    &__content_gallery_elem_desc {
        font-size: 16px;
        font-weight: 400;
        ul {
            text-align: left;
            margin-left: 40px;
            margin-top: 5px;
            margin-bottom: 10px;
            list-style: none;
            li  {
                display: flex;
                align-items: center;
                &:before {
                    content: '';
                    padding-left: 3px;
                    height: 5px;
                    width: 5px;
                    background-color: black;
                    border-radius: 5px;
                    display: block;
                    margin-right: 5px;
                }
            }
        }
    }
    &__content {
        &_title {
            font-size: 25px;
            font-weight: bold;
            margin: 10px 0;
        }
        &_gallery_elem {
            &_wrap_btn {
                margin: 5px 0;
            }
            &_name {
            font-size: 25px;
            font-weight: bold;
            }
            &_btn {
                margin: 5px 0;
                font-size: 22px;
                font-weight: bold;
                border-radius: 5px;
                color: white;
                background-color: red;
                box-shadow: 1px 5px #790808;
                outline: none;
                border: none;
                transition: all 0.4s;
                &:active {
                    margin-top: 10px;
                    box-shadow: 0 0 #02024a;
                    margin-bottom: 0;
                }
            }
        }
        &_more {
            margin: 10px 0;
        }
    }
}

/* designer */
.show_constuctor {
    .designer__opacity-layout {
        display: none;
    }
    .designer__constructor-layout {
        display: block
    }
}

.designer {
    text-align: center;
    background-color: #dfdfdf;
    &__options {
        text-align: left;
        padding: 45px;
        width: 30%;
    }
    &__title {
        font-size: 40px;
    }
    &__opacity-layout {
        min-height: 286px;
    }
    &__constructor-layout {
        &-description {
            max-width: 350px;
            width: 100%;
            margin: 0 auto;
        }
        display: none;
    }
    &__constructor {
        width: 70%;
    }
    &__constructor-elem {
        transform: rotateY(90deg);
        transition: all 0.3s;
        display: none;
        height: 100%;
        width: 100%;
        &-pipe {
            transform: rotateY(90deg);
            transition: all 0.3s;
            //height: 100%;
            //width: 100%;
            max-width: 66px;
            display: none;
        }
    }
    &__constructor-layout-top {
        min-height: 100px;
        img {
            margin: 0 auto;
        }
    }
    &__constructor-layout-bottom {
        width: fit-content;
        margin: 0 auto;
    }
}

/* material */

.material {
	&__slider {
		.cont(960px, 20px);
		&_img {
			border-radius: 5px;
    		border: 1px solid #ada9a9;
		}
	}
	&__slider-title {
		font-size: 25px;
	}
}

/* .footer */
.footer {
    background-color: black;
    color: white;
}


/* .fancybox */
.fancybox {
    &-navigation .fancybox-button--arrow_right {
    	margin-right: 80px;
    	.md(1280px, {
    		margin-right: 0;
    	});
    }

    &-navigation .fancybox-button--arrow_left {
    	margin-left: 80px;
    	.md(1280px, {
    		margin-left: 0;
    	});
    }
}
